import GQL from '../helpers/GQL'


const Produk = {
	listProduk: (clientID, token, kodeklinik) => {
		return new Promise((resolve, reject) => {
			const query = `
                query {
                    listProduk(kodeklinik:"${kodeklinik}"){
                        kodeklinik,
                        kodeProduk,
                        namaProduk,
                        satuan
                        harga,
                        kategori{
                            kodeKategoriProduk,
                            kategoriProduk
                        },
                        jenis{
                            kodeJenisProduk,
                            jenisProduk,
                        }
                      }
                }
            `
			GQL(query, clientID, token).then(resp => {
                const listDatas = []
                resp.data.listProduk.map(el => {
                    // console.log(el)
                    if (el.kategori) {
                        var kategori = el.kategori.kategoriProduk
                        var kodekategori = el.kategori.kodeKategoriProduk
                    } 

                    if (el.jenis) {
                        var jenis = el.jenis.jenisProduk
                        var kodejenis = el.jenis.kodeJenisProduk
                    } 

                  return listDatas.push({
                        ...el,
                        kodekategori,
                        kategori,
                        kodejenis,
                        jenis,
                    })
                })
				if (resp.errors) return (reject(resp.errors));;
				if (resp.data && resp.data.listProduk) return (resolve(listDatas));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
    },
    simpanProduk: (clientID, token,  kodeklinik, kodeProduk, Produk, jenis, kategori, harga, satuan) => {
        return new Promise((resolve, reject) => {
            const query =`
            mutation {
                simpanProduk(input:{
                    kodeklinik: "${kodeklinik}",
                    kodeProduk: "${kodeProduk}",
                    namaProduk: "${Produk}",
                    kategori: "${kategori}",
                    jenis: "${jenis}",
                    harga: ${harga},
                    satuan:"${satuan}"
                })
            }
        `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.simpanProduk) return (resolve(resp.data.simpanProduk));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
	hapusProduk: (clientID, token, kodeklinik, kodeProduk) => {
		return new Promise((resolve, reject) => {
			const query = `
			mutation {
                hapusProduk(kodeProduk:"${kodeProduk}", kodeklinik:"${kodeklinik}")
			  }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));
				if (resp.data && resp.data.hapusProduk ) return (resolve(resp.data.hapusProduk));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
    },
    loadSelecBox: (clientID, token, kodeklinik) => {
		return new Promise((resolve, reject) => {
			const query = `
                query {
                    listKategoriProduk(kodeklinik:"${kodeklinik}"){
                        kodeKategoriProduk
                        kategoriProduk
                      }
                      listJenisProduk(kodeklinik:"${kodeklinik}"){
                        kodeJenisProduk
                        jenisProduk
                      }
                }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));;
                const kategori = resp.data.listKategoriProduk 
                const jenis = resp.data.listJenisProduk 
				if (resp.data && resp.data.listKategoriProduk) return (resolve({kategori, jenis}));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
    },
};

export default Produk;
