import React, { useState, useEffect } from 'react'
import { Row, Col, Modal, ModalHeader, ModalBody, ModalFooter, Button } from "reactstrap";
import { TextField } from '@material-ui/core';
import mProduk from '../../../../Controler/c_Produk'
import validate from 'validate.js'
import Notifikasi from '../../../../helpers/Notifikasi'
import { isArray } from "util";
import IntlMessages from "../../../../helpers/IntlMessages"
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';

const ModalAdmin = (props) => {
    const clientID = props.clientID
    const token = props.token
    const detail = props.detailProduk || {}
    const [listKategori, setlistKategori] = useState([])
    const [listJenis, setlistJenis] = useState([])
    const klinik = props.klinik
    // console.log(detail, 'detail')
    const [form, setForm] = useState({
        kodeProduk: '',
        namaProduk: '',
        jenis: '',
        kategori: '',
        harga: 0,
        satuan:''
    })
    useEffect(() => {
        if (detail.kodeProduk) {
            setForm({
                kodeProduk: detail.kodeProduk,
                namaProduk: detail.namaProduk,
                jenis: detail.kodejenis,
                kategori: detail.kodekategori,
                harga: detail.harga,
                satuan: detail.satuan
            })
        }
        mProduk.loadSelecBox(clientID, token, klinik.kodeklinik).then(resp => {
            setlistKategori(resp.kategori);
            setlistJenis(resp.jenis);
        }).catch(err => {
            console.error(err);
        })
        return () => {

        }
    }, [clientID, token, klinik.kodeklinik, detail])
    const handleClose = () => {
        props.setOpen(false)
    }
    const handleChange = (ev) => {
        var value = ev.target.value;
        const key = ev.target.id || ev.target.name;
        var newForm = { ...form }
        newForm[key] = value;
        // console.log(newForm, 'newForm')
        setForm(newForm);
    }
    const validateForm = () => {
        const { kodeProduk, namaProduk, jenis, kategori, harga, satuan} = form;
        const promForm = new Promise((resolve, reject) => {
            const constraint = {
                namaProduk: {
                    length: {
                        minimum: 3,
                        message: "Nama Produk produk minimal terdiri dari 3 huruf"
                    },
                },
                kategori: {
                    length: {
                        minimum: 3,
                        message: "Kategori produk wajib isi"
                    },
                },
                jenis: {
                    length: {
                        minimum: 3,
                        message: "Jenisp roduk produk wajib isi"
                    },
                },
                satuan: {
                    length: {
                        minimum: 1,
                        message: "Satuan produk wajib isi"
                    },
                }
            }
            const validator = validate({ kodeProduk, namaProduk, jenis, kategori, harga, satuan }, constraint, { fullMessages: false });
            if (validator === undefined) {
                resolve();
            } else {
                reject(validator);
            }
        })
        return Promise.all([promForm]);
    }
    const handleSimpan = () => {
        validateForm().then(() => {
            mProduk.simpanProduk(clientID, token, klinik.kodeklinik, form.kodeProduk, form.namaProduk, form.jenis, form.kategori, form.harga, form.satuan).then(resp => {
                if (resp) {
                    Notifikasi("success", "Berhasil disimpan", "")
                    props.setIsReload(true)
                    handleClose()
                } else {
                    Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
                }
            }).catch(err => {
                if (isArray(err) && err.length > 0) {
                    Notifikasi("error", err[0].message, "Gagal Simpan")
                } else if (typeof err) {
                    Notifikasi("error", err.message, "Gagal Simpan")
                } else {
                    console.log(err);
                }
            })
        }).catch(err => {
            const keys = Object.keys(err);
            for (const key of keys) {
                Notifikasi("warning", err[key])
            }
        })
    }
    return (
        <Modal
            isOpen={props.open}
            toggle={handleClose}
            wrapClassName="modal-right"
        >
            <ModalHeader toggle={handleClose}>
                {detail.kodeProduk ? ("Update") : ("Add")} <IntlMessages id="menu.produk" />
                    </ModalHeader>
            <ModalBody>
                <Row>
                    <Col xs={12} md={12} className="marginFrom">
                        <TextField
                            id="namaProduk"
                            name="namaProduk"
                            label="Nama Produk"
                            fullWidth
                            value={form.namaProduk}
                            onChange={handleChange}
                            variant="outlined"
                        />
                    </Col>
                </Row>
                <Row>
                    <Col xs={12} md={12} className="marginFrom">
                        <FormControl fullWidth>
                            <InputLabel id="kategori">Kategori</InputLabel>
                            <Select
                            labelId="kategori"
                            id="kategori"
                            name="kategori"
                            value={form.kategori}
                            onChange={handleChange}
                            
                            >
                            {listKategori.map(el => (
                                <MenuItem key={el.kodeKategoriProduk} value={el.kodeKategoriProduk}>{el.kategoriProduk}</MenuItem>
                            ))}
                            </Select>
                        </FormControl>
                    </Col>
                </Row>
                <Row>
                    <Col xs={12} md={12} className="marginFrom">
                        <FormControl fullWidth>
                            <InputLabel id="jenis">Jenis</InputLabel>
                            <Select
                            labelId="jenis"
                            id="jenis"
                            name="jenis"
                            value={form.jenis}
                            onChange={handleChange}
                            
                            >
                            {listJenis.map(el => (
                                <MenuItem key={el.kodeJenisProduk} value={el.kodeJenisProduk}>{el.jenisProduk}</MenuItem>
                            ))}
                            </Select>
                        </FormControl>
                    </Col>
                </Row>
                <Row>
                    <Col xs={12} md={12} className="marginFrom">
                        <TextField
                            id="harga"
                            name="harga"
                            label="Harga"
                            fullWidth
                            value={form.harga}
                            onChange={handleChange}
                            variant="outlined"
                            type="number"
                        />
                    </Col>
                    <Col xs={12} md={12} className="marginFrom">
                        <TextField
                            id="satuan"
                            name="satuan"
                            label="Satuan"
                            fullWidth
                            value={form.satuan}
                            onChange={handleChange}
                            variant="outlined"
                        />
                    </Col>
                </Row>
            </ModalBody>
            <ModalFooter>
                <Button color="secondary" onClick={handleClose}>Cancel</Button>
                <Button color="primary" onClick={handleSimpan}>Save <IntlMessages id="menu.produk" /></Button>{" "}
            </ModalFooter>
        </Modal>
    )
}

export default ModalAdmin;